<template>
    <div id="swe-dashboard">
        <h3>
            Time In and Time out of SWE
        </h3>
        <div class="time-in-out">
            <button  v-if="!timedIn" @click="timeIn" class="btn black">Time-in</button>
             <button  v-else-if="timedIn" @click="timeOut" class="btn black">Time-out</button>           
        </div>
        <div v-if="!timedIn"> </div>
        <div v-else>
            You have timed in : {{timeinDisplay}}
        </div>
        <div v-if="!timedOut"> </div>
        <div v-else>
            You have timed out : {{timeout}}
        </div>

    </div>
</template>

<script>
import firebase from 'firebase'
import db from '../firebase'
import {mapGetters, mapActions} from 'vuex';


export default {
    name: 'swe-dashbaord',
    data(){
        return {
            timedIn: false,
            timeinDisplay: '',
            timedOut: false,
            timeout: '',
            timeref: '',
        }
    },
    computed: {
  
    },
    methods: {
        timeIn(e){
            this.timeinDisplay = new Date().toLocaleString()

            db.collection('Timsheet').add({
                Name: firebase.auth().currentUser.email,
                Timein: new Date(),
                Timeout: '',
                NightDiff: '',
                TotalHours: '',
                Overtime: '',
                Remarks: '',
            }).then(docRef => {
                this.timedIn = true
                this.timein = new Date()
                this.timeref = docRef.id
                console.log(this.timeref)
                alert('you have successfully timed in')
            })
            e.preventDefault()
        },
        timeOut(e){
            let date1 = this.timein
            let date2 = new Date()
            let hours = Math.abs((date2.getTime() - date1.getTime()) / 36e5)
            let remarks = 'Undertime'
            let ndHoursin = 0;
            let ndHoursout = 0;
            const date1Convertion = parseInt(date1.toLocaleString('en-US',{hour: 'numeric', hour12: true}))
            const date2Convertion = parseInt(date2.toLocaleString('en-US',{hour: 'numeric', hour12: true}))
            const tempNdHoursin = 0;
            if(date1Convertion < 10){
                ndHoursin = 10
            }else{
                ndHoursin = date1Convertion
            }
           

            if(9 > date2Convertion && date2Convertion > 6){
                 ndHoursout = 6 + 12 
                 console.log('if block')
            }else if( date2Convertion > 9){
                ndHoursout = date2Convertion
                console.log('else if block')
            }else if( date2Convertion < 6){
                ndHoursout = date2Convertion + 12
                console.log ('else if block 2')
            }
            

            
            console.log(ndHoursin + ' ' + ndHoursout)
            const time = (ndHoursout - ndHoursin)
            console.log(time)
            db.collection('Timsheet').doc(this.timeref).update({
                Timeout: new Date(),
                TotalHours: hours.toFixed(2),
                NightDiff: time,
            }).then(()=>{
                this.timedOut = true
                this.timeout = new Date().toLocaleString()
                alert('you have successfully timed out')
                if(hours < 9) {
                    db.collection('Timsheet').doc(this.timeref).update({
                        Remarks: remarks.toUpperCase()
                        })
                    }
                else if(hours > 9){
                    let remarks = 'overtime'
                    db.collection('Timsheet').doc(this.timeref).update({
                        Remarks: remarks.toUpperCase()
                        })
                    }

                firebase.auth().signOut().then(()=>{
                this.$router.push('/login')
                    })
                }).catch(function(error){
                alert("Error in updating", error)
            })
            e.preventDefault()
        }
    }
}
</script>

<style>

</style>



